import React from 'react'
import { Layout } from 'react-grid-layout'
import styled from 'styled-components'

import { GridLayout } from '../atoms/GridLayout'
import { PageTitle } from '../atoms/PageTitle'
import { SoundControl } from '../atoms/SoundControl'
import { LogoutWidget } from '../organisms/LogoutWidget'
import { SecureRoute } from '../routes/SecureRoute'
import { HomeButton } from './HomeButton'

export interface IPageLayoutProps {
  pageTitle?: string
  layouts?: Layout[]
  hideLogoutWidget?: boolean
  showSoundControl?: boolean
  useBackButton?: boolean
  backRoute?: string
  scrollPageTitle?: boolean
  topContent?: React.ReactNode
}

const TopLeftButtonContainer = styled.div`
  position: fixed;
  top: 0;
  left: 0;
`

const getPosition = (props: IPageLayoutProps) =>
  props.scrollPageTitle ? 'absolute' : 'fixed'

const TopRightContainer = styled.div`
  position: ${getPosition};
  top: 0;
  right: 0;
  z-index: 100;
  display: flex;
  justify-content: flex-end;
`
const PageTitleContainer = styled.div`
  position: ${getPosition};
  top: 0rem;
  left: 4rem;
`
const PageHeaderItem = styled.div`
  margin: 0.5rem;
`

export const PageLayout: React.FC<IPageLayoutProps> = ({
  pageTitle = '',
  layouts = [],
  hideLogoutWidget = false,
  showSoundControl = false,
  useBackButton = false,
  scrollPageTitle = true,
  backRoute,
  children,
  topContent,
}) => (
  <>
    {pageTitle && <PageTitle title={pageTitle} />}

    <TopRightContainer scrollPageTitle={scrollPageTitle}>
      <PageHeaderItem>{topContent}</PageHeaderItem>
      {showSoundControl && (
        <PageHeaderItem>
          <SoundControl />
        </PageHeaderItem>
      )}

      {!hideLogoutWidget && (
        <SecureRoute path="/" component={LogoutWidget} redirect={false} />
      )}
    </TopRightContainer>

    <PageTitleContainer scrollPageTitle={scrollPageTitle}>
      <PageTitle.Display />
    </PageTitleContainer>

    <GridLayout layouts={[...layouts]}>{children}</GridLayout>

    <TopLeftButtonContainer>
      <HomeButton goBack={useBackButton} backRoute={backRoute} />
    </TopLeftButtonContainer>
  </>
)
