import { createDefaultApolloClient, createNetworkLink } from '@speedlo/graphql'
import QueueLink from 'apollo-link-queue'
import { RetryLink } from 'apollo-link-retry'
import { autorun } from 'mobx'

import { appConfig } from '../config'
import { logGraph } from '../helpers/logger'
import { isNetworkOnline } from '../helpers/offline'
import { TAuthModel } from '../models/AuthModel'
import createAuthLink from './AuthLink'

export function createApolloClient(auth: TAuthModel) {
  return createDefaultApolloClient(
    createAuthLink(auth),
    createRetryLink(),
    createOfflineLink(),
    createNetworkLink(appConfig.endpoint),
  )
}

function createRetryLink() {
  return new RetryLink({
    delay: {
      initial: 300,
      max: Infinity,
      jitter: true,
    },
    attempts: {
      max: 5,
      retryIf: error => !!error,
    },
  })
}

function createOfflineLink() {
  const offlineLink = new QueueLink()
  autorun(() => {
    if (isNetworkOnline()) {
      logGraph('client is online')
      offlineLink.open()
    } else {
      logGraph('client is offline')
      offlineLink.close()
    }
  })
  return offlineLink
}
