import { ApolloProvider, DefaultApolloClient } from '@speedlo/graphql'
import { useAsyncEffect } from '@speedlo/hooks'
import React from 'react'

import { RootContext } from '../context/RootContext'
import { createApolloClient } from '../graph/createClient'
import { persistModel } from '../helpers/persistModel'
import { AuthModel } from '../models/AuthModel'
import { RootModel } from '../models/RootModel'
import PageLoader from './PageLoader'

export const RootProvider: React.FC = ({ children }) => {
  const [isInit, setInit] = React.useState(false)
  const [root] = React.useState(() => {
    const auth = AuthModel.create({})
    const apolloClient = createApolloClient(auth)
    return RootModel.create(
      { auth },
      {
        apolloClient,
      },
    )
  })

  useAsyncEffect(async () => {
    if (process.env.REACT_APP_STAGE === 'dev') {
      // @ts-ignore
      window.root = root
    }
    await Promise.all(root.persistedModels.map(persistModel))
    setInit(true)
  }, [root])

  if (!isInit) {
    return <PageLoader />
  }

  return (
    <ApolloProvider client={root.client}>
      <RootContext.Provider value={root}>
        {React.Children.only(children)}
      </RootContext.Provider>
    </ApolloProvider>
  )
}

export const useRoot = () => {
  const root = React.useContext(RootContext)
  if (!root) {
    throw new Error('RootProvider missing in the tree')
  }
  return root
}

export interface IRootEnvironment {
  fetch: TFetch
  apolloClient: DefaultApolloClient
}
