import { Observer } from 'mobx-react-lite'
import React from 'react'

import { TRootModel } from '../models/RootModel'
import { useRoot } from './RootProvider'

interface IProps {
  render(root: TRootModel): ReactNode
  observe?: boolean
}

export const WithRoot: React.FC<IProps> = ({ render, observe = true }) => {
  const root = useRoot()
  return observe === true ? (
    <Observer>{() => <>{render(root)}</>}</Observer>
  ) : (
    render(root)
  )
}
