import React from 'react'

import { TouchButton } from '../molecules/TouchButton'
import { Colors } from '../styling'
import { SizerButton } from './SizerButton'
import { WithRoot } from './WithRoot'

export class SoundControl extends React.Component {
  state = { playPreview: false }
  render() {
    return (
      <WithRoot
        observe={true}
        render={({ user }) => (
          <SizerButton>
            <TouchButton
              color={user.soundEnabled ? Colors.green : Colors.red}
              iconName={user.soundEnabled ? 'volume_up' : 'volume_off'}
              iconSize={2}
              onClick={() => {
                user.controlSound(!user.soundEnabled)
              }}
            />
          </SizerButton>
        )}
      />
    )
  }
}
