import { DocumentNode } from 'graphql'
import { Lambda } from 'mobx'
import React, { useCallback, useEffect, useRef } from 'react'
import { Query, QueryProps, QueryResult } from 'react-apollo'

import { Loading } from '../atoms/Loading'
import { whenGoingOnline } from '../helpers/offline'

export function buildQuery<TData, TVariables>(query: DocumentNode) {
  type TResult = QueryResult<TData, TVariables>
  type TRenderProp<T> = (data: TData, result: TResult) => T
  interface IProps
    extends Omit<QueryProps<TData, TVariables>, 'query' | 'children'> {
    render?: TRenderProp<ReactNode>
    renderFragment?: TRenderProp<ReactNode[]>
    renderLoading?: boolean | ((result: TResult) => ReactNode)
    renderError?: boolean
    refetchOnConnectionLost?: boolean
  }
  const GQuery: React.FC<IProps> = props => {
    const cleanup = useRef<Lambda[]>([])
    const executeCleanup = useCallback(() => {
      cleanup.current.forEach(fn => fn())
      cleanup.current.length = 0
    }, [])

    useEffect(
      () => {
        return executeCleanup
      },
      [cleanup.current.length],
    )

    const {
      render,
      renderFragment,
      renderLoading = true,
      renderError = true,
      refetchOnConnectionLost = false,
      children,
      ...rest
    } = props

    return (
      // @ts-ignore Some weird changes in @types/react
      <Query query={query} {...rest}>
        {result => {
          if (result.loading) {
            if (typeof renderLoading === 'function') {
              return renderLoading(result)
            } else if (renderLoading === true) {
              return <Loading />
            }
          }
          if (result.error) {
            throw result.error
          }
          if (refetchOnConnectionLost) {
            cleanup.current.push(whenGoingOnline(result.refetch))
          }
          if (result.data) {
            if (renderFragment) {
              return <>{renderFragment(result.data, result)}</>
            }
            if (render) {
              return render(result.data, result)
            }
          }
          return children || null
        }}
      </Query>
    )
  }
  return GQuery
}
