import { useSnackbar as useSnackbarOrig, VariantType } from 'notistack'

const successVariant = { variant: 'success' as VariantType }
const errorVariant = { variant: 'error' as VariantType }
const warningVariant = { variant: 'warning' as VariantType }
const infoVariant = { variant: 'info' as VariantType }

export function useSnackBar() {
  const { enqueueSnackbar } = useSnackbarOrig()
  return {
    enqueueSnackbar,
    enqueueSuccess: (errorMessage: string) =>
      enqueueSnackbar(errorMessage, successVariant),
    enqueueError: (errorMessage: string) =>
      enqueueSnackbar(errorMessage, errorVariant),
    enqueueWarning: (errorMessage: string) =>
      enqueueSnackbar(errorMessage, warningVariant),
    enqueueInfo: (errorMessage: string) =>
      enqueueSnackbar(errorMessage, infoVariant),
  }
}
