import { endOfDay, startOfDay } from 'date-fns'
import { types } from 'mobx-state-tree'

import { BaseModel } from '../../../models/BaseModel'

export interface IStatisticsDateFilter {
  dateTimeFrom: Date
  dateTimeTo: Date
}

export const StatisticsModel = BaseModel.named('Statistics')
  .props({
    branches: types.optional(types.array(types.string), []),
    drivers: types.optional(types.array(types.string), []),
    closureBranch: types.maybeNull(types.string),
  })
  .volatile<IStatisticsDateFilter>(() => ({
    dateTimeFrom: startOfDay(new Date()),
    dateTimeTo: endOfDay(new Date()),
  }))
  .views(self => ({
    get branchesFilter() {
      return self.branches.length > 0
        ? Array.from(self.branches)
        : self.root.user.branchesId
    },
    get hasBranchFilter() {
      return self.branches.length > 0
    },
  }))
  .actions(self => ({
    updateFilter(formValues: IStatisticsDateFilter) {
      self.dateTimeFrom = new Date(formValues.dateTimeFrom)
      self.dateTimeTo = new Date(formValues.dateTimeTo)
    },
    clearBranchFilter() {
      self.branches.clear()
    },
    setBranchFilter(branches: ID[]) {
      self.branches.replace(branches)
    },
    setDriversFilter(drivers: ID[]) {
      self.drivers.replace(drivers)
    },
    setClosureBranch(branch: MaybeID) {
      self.closureBranch = branch
    },
    setTodayOrdersFilter() {
      self.dateTimeFrom = startOfDay(new Date())
      self.dateTimeTo = endOfDay(new Date())
      self.branches.replace(self.root.order.list.branchesFilter)
    },
  }))
