import { observer } from 'mobx-react-lite'
import React from 'react'
import { Link } from 'react-router-dom'
import styled from 'styled-components'

import { Icon } from '../atoms/Icon'
import { useRoot } from '../atoms/RootProvider'
import { useQLogoutWidget } from '../graph/generated'
import { i18n } from '../i18n'
import { Colors } from '../styling'

const LogoutWidgetStyled = styled.div`
  margin-left: 1rem;
  color: ${Colors.greyLight};
  &:hover {
    color: ${Colors.white};
  }
`
const LoggedUserNameStyled = styled.span`
  color: ${Colors.greyLight};
  text-decoration: none;
  font-weight: lighter;
  position: relative;
  top: 0.6rem;
  display: inline-block;
  max-width: 10rem;
  text-align: right;
`

const IconStyled = styled.span`
  padding: 0.5rem 0.5rem 0 0.5rem;
  display: inline-block;
  vertical-align: top;
`

export const LogoutWidget = observer(() => {
  const { auth } = useRoot()
  const { data, loading } = useQLogoutWidget(
    { id: auth.userId },
    { skip: !auth.isAuthenticated },
  )

  let displayName
  if (loading) {
    displayName = i18n.t`Not logged in`
  } else {
    displayName = `${data.user.firstName} ${data.user.lastName}`
  }

  return (
    <LogoutWidgetStyled data-testid="logout-widget">
      <Link to="/logout">
        <LoggedUserNameStyled>{displayName}</LoggedUserNameStyled>
        <IconStyled>
          <Icon iconName="exit_to_app" />
        </IconStyled>
      </Link>
    </LogoutWidgetStyled>
  )
})
