import '@speedlo/assets/flaticon.css'
import 'material-design-icons/iconfont/material-icons.css'
import 'react-grid-layout/css/styles.css'

import { useReleaseCabinet } from '@speedlo/tools/dist/useReleaseCabinet'
import { Observer } from 'mobx-react-lite'
import React from 'react'

import { ConnectionWarning } from '../atoms/ConnectionWarning'
import PageLoader from '../atoms/PageLoader'
import { useRoot } from '../atoms/RootProvider'
import { useSnackBar } from '../hooks/useSnackBar'
import { Routing } from '../routes/Routing'
import { DefaultErrorHandler } from './DefaultErrorHandler'
import { GlobalStyles } from './GlobalStyles'
import { Router } from './Router'
import { UpdateHandler } from './UpdateHandler'
import { UserBootstrap } from './UserBootstrap'

export const AppMain: React.FC = () => {
  const { user } = useRoot()

  const { enqueueInfo } = useSnackBar()
  useReleaseCabinet(enqueueInfo)

  return (
    <DefaultErrorHandler>
      <GlobalStyles />
      <UserBootstrap />
      <UpdateHandler />
      <ConnectionWarning />
      <React.Suspense fallback={<PageLoader />}>
        <Router>
          <Observer>
            {() => (user.isReady ? <Routing /> : <PageLoader />)}
          </Observer>
        </Router>
      </React.Suspense>
    </DefaultErrorHandler>
  )
}
