export interface IQueryError extends Error {
  showToUser: boolean
}

export type TQueryErrors = IQueryError[] | null

// tslint:disable-next-line:no-any
export const getQueryErrors = async (query: Promise<any>) => {
  const result = await query
  if (result && result.errors) {
    return result.errors as IQueryError[]
  }
  return null
}
