import { isoDate } from '@speedlo/graphql'
import { startOfMinute } from 'date-fns'
import _ from 'lodash'
import { Instance, SnapshotIn, types } from 'mobx-state-tree'

import { ISuggestion } from '../../../components/AddressSuggest'
import {
  DeliveryTypeEnum,
  MCreateOrderAcceptVariables,
  MCreateOrderNewVariables,
  MCreateOrderUpdateVariables,
} from '../../../graph/generated'
import { GOrderVam, SOrderCall } from '../../../graph/types'
import { faker } from '../../../helpers/faker'
import { RM } from '../../../helpers/ramda'
import { BaseModel } from '../../../models/BaseModel'

export interface IOrderSidedishControlResult {
  sidedishes: ReadonlyArray<ID>
  isConfirmed: boolean
}

export interface ICall extends Omit<SOrderCall.Call, 'isRinging' | 'eventId'> {
  eventId?: ID
}

export interface IOrderFormValues {
  deliveryType: DeliveryTypeEnum.Messenger | DeliveryTypeEnum.Pickup
  deliverAt?: Date
  phone?: string
  phonePrefix?: string
  firstName?: string
  lastName?: string
  email?: string
  note?: string
}

interface IVolatileProps {
  orderId: MaybeID
  call?: ICall
  deliveryAddress?: ISuggestion
  deliveryMinutes?: number
  minimalPrice?: string
  notDeliveryLocation: boolean
  values: IOrderFormValues
  fakeValues?: Partial<IOrderFormValues>
  fakeAddress?: ISuggestion
  vamCustomer: Maybe<GOrderVam.VamCustomer>
  sidedishMode: 'none' | 'recipe' | 'item'
  sidedishRootId?: ID
  sidedishSelection?: ReadonlyArray<ID>
}

export const CreateOrderModel = BaseModel.named('CreateOrder')
  .props({
    branchId: types.maybe(types.string),
  })
  .volatile<IVolatileProps>(getDefaultProps)
  .views(self => ({
    get currentBranch() {
      return (
        self.root.user.branches.find(RM.propEq('id', self.branchId)) ||
        self.root.user.defaultBranch
      )
    },
  }))
  .views(self => ({
    get deliveryType() {
      return self.values.deliveryType
    },
    get isDelivery() {
      return self.values.deliveryType === DeliveryTypeEnum.Messenger
    },
    get deliverAt() {
      return (self.values.deliverAt && new Date(self.values.deliverAt)) || null
    },
    get deliverAtIso() {
      return (this.deliverAt && isoDate(startOfMinute(this.deliverAt))) || null
    },
    get hasAddress() {
      return Boolean(self.deliveryAddress || self.fakeAddress)
    },
    get gps() {
      if (this.isDelivery && this.hasAddress) {
        if (self.deliveryAddress && self.deliveryAddress.gps) {
          return clearGps(self.deliveryAddress.gps)
        } else if (self.fakeAddress && self.fakeAddress.gps) {
          return clearGps(self.fakeAddress.gps)
        }
      }
      return { ...clearGps(self.currentBranch.gps) }
    },
    get phone() {
      if (self.values.phone !== undefined) {
        return self.values.phone
      }
      return self.call ? self.call.phone : ''
    },
    get phonePrefix() {
      if (self.values.phonePrefix !== undefined) {
        return self.values.phonePrefix
      }
      if (self.call) {
        return self.call.prefix
      }
      if (self.currentBranch.address.country) {
        return self.currentBranch.address.country.phonePrefix
      }
      return '+'
    },
    get customer() {
      const formValues = this.formValues as IOrderFormValues
      return {
        firstName: formValues.firstName,
        lastName: formValues.lastName,
        emails: formValues.email ? [{ email: formValues.email }] : null,
        phones: formValues.phone
          ? [
              {
                phone: `${formValues.phonePrefix}${formValues.phone}`,
              },
            ]
          : null,
      }
    },
    get formValues() {
      if (self.vamCustomer !== null && self.vamCustomer.isFound) {
        const { firstName, lastName, email, lastOrder } = self.vamCustomer
        return _.defaults(
          { ...self.values },
          {
            phone: this.phone,
            phonePrefix: this.phonePrefix,
            firstName,
            lastName,
            email,
            note: (lastOrder && lastOrder.note) || '',
          },
        )
      }
      if (self.fakeValues) {
        return _.defaults({ ...self.values }, { ...self.fakeValues })
      }
      return _.defaults(
        { ...self.values },
        { phone: this.phone, phonePrefix: this.phonePrefix },
      )
    },
    get hasOrder() {
      return self.orderId !== null
    },
  }))
  .views(self => ({
    get createOrderVariables(): MCreateOrderNewVariables {
      return {
        branchId: self.currentBranch.id,
        deliveryType: self.values.deliveryType,
        deliverAt: self.deliverAtIso,
        callEventId: self.call && self.call.eventId,
        gps: self.gps,
      }
    },
    get updateOrderVariables(): MCreateOrderUpdateVariables {
      return {
        orderId: self.orderId!,
        deliveryType: self.deliveryType,
        deliverAt: self.deliverAtIso,
        customer: self.customer,
        gps: self.gps,
      }
    },
    get acceptOrderVariables(): MCreateOrderAcceptVariables {
      return {
        orderId: self.orderId!,
        customer: self.customer,
        deliverAt: self.deliverAtIso,
        note: self.formValues.note,
      }
    },
  }))
  .actions(self => ({
    resetOrder() {
      self.orderId = null
    },
    resetModel() {
      Object.assign(self, getDefaultProps())
    },
    setOrder(orderId: ID) {
      self.orderId = orderId
      return orderId
    },
  }))
  .actions(self => ({
    useLocation(address: ISuggestion) {
      self.deliveryAddress = address
    },
    clearLocation() {
      self.deliveryAddress = undefined
    },
    setMinimalPrice(price: string | undefined) {
      self.minimalPrice = price
    },
  }))
  .actions(self => ({
    selectBranch(branchId: ID) {
      self.branchId = branchId
    },
    userIsCalling(call: ICall | undefined) {
      self.call = call
    },
    setDeliveryMinutes(minutes: number | undefined) {
      self.deliveryMinutes = minutes
    },
    updateFormValues(
      values: IOrderFormValues,
      touched: { [K in keyof IOrderFormValues]?: boolean },
    ) {
      self.values = {
        deliveryType: values.deliveryType,
        ..._.pick(values, Object.keys(touched)),
        deliverAt: values.deliverAt,
      }
      self.log('updated values: %j', self.values)
    },
    updateFromVam(vamCustomer: Maybe<GOrderVam.VamCustomer>) {
      self.vamCustomer = vamCustomer
      self.log('vam customer: %j', vamCustomer)
    },
    updateFormWithFakeValues() {
      self.fakeValues = {
        firstName: faker.name.firstName(),
        lastName: faker.name.lastName(),
        email: faker.internet.email(),
        phone: faker.phone.phoneNumberFormat(3).replace(/\s/g, ''),
        phonePrefix: '+420',
        note: faker.lorem.sentence().slice(0, 50 - 1),
      }
      self.fakeAddress = {
        state: '',
        city: 'Hradec Krlov',
        street: 'Opletalova',
        houseNumber: '334/2',
        gps: {
          latitude: 50.2129839,
          longitude: 15.8342836,
        },
      }
    },
  }))
  .actions(self => {
    let resolver: Maybe<(result: IOrderSidedishControlResult) => void> = null
    const makeResolver = () => {
      return new Promise<IOrderSidedishControlResult>(resolve => {
        resolver = resolve
      })
    }
    return {
      selectSidedishesForRecipe(recipeId: ID) {
        self.sidedishMode = 'recipe'
        self.sidedishRootId = recipeId
        return makeResolver()
      },
      selectSidedishesForItem(itemId: ID) {
        self.sidedishMode = 'item'
        self.sidedishRootId = itemId
        return makeResolver()
      },
      sidedishSelectionConfirmed(sidedishes: ReadonlyArray<ID>) {
        self.sidedishMode = 'none'
        self.sidedishRootId = undefined
        if (resolver) {
          resolver({
            isConfirmed: true,
            sidedishes,
          })
          resolver = null
        }
      },
      sidedishSelectionCancel() {
        self.sidedishMode = 'none'
        self.sidedishRootId = undefined
        if (resolver) {
          resolver({
            isConfirmed: false,
            sidedishes: [],
          })
          resolver = null
        }
      },
    }
  })

function getDefaultProps(): IVolatileProps {
  return {
    orderId: null,
    call: undefined,
    values: {
      deliveryType: DeliveryTypeEnum.Messenger,
      deliverAt: undefined,
      phone: undefined,
      email: undefined,
      firstName: undefined,
      lastName: undefined,
      note: undefined,
    },
    fakeAddress: undefined,
    fakeValues: undefined,
    deliveryAddress: undefined,
    deliveryMinutes: undefined,
    minimalPrice: undefined,
    vamCustomer: null,
    notDeliveryLocation: false,
    sidedishMode: 'none',
    sidedishRootId: undefined,
    sidedishSelection: undefined,
  }
}

function clearGps({ latitude, longitude }: GeoPoint) {
  return { latitude, longitude }
}

export interface TCreateOrderModel extends Instance<typeof CreateOrderModel> {}
export interface TCreateOrderModelProps
  extends SnapshotIn<typeof CreateOrderModel> {}
