import React from 'react'
import { Route, RouteComponentProps, RouteProps } from 'react-router-dom'

interface IProps<Params> extends Omit<RouteProps, 'render' | 'children'> {
  component: React.ComponentType<RouteComponentProps<Params>>
}

export function LazyRoute<Params = {}>({
  component: Component,
  ...props
}: IProps<Params>) {
  return (
    <Route {...props} render={routeProps => <Component {...routeProps} />} />
  )
}
