import { Instance, SnapshotIn, types } from 'mobx-state-tree'
import * as R from 'ramda'

import {
  FCompanyBranchBrand,
  FCompanyBranchFragment,
  QUserBootstrapAccess,
  QUserBootstrapFields,
  QUserBootstrapPermissions,
  QUserBootstrapUser,
} from '../graph/generated'
import { RM } from '../helpers/ramda'
import { BaseModel } from '../models/BaseModel'

interface IVolatileProps {
  isLoaded: boolean
  branches: ReadonlyArray<FCompanyBranchFragment>
  brands: ReadonlyArray<FCompanyBranchBrand>
  soundEnabled: boolean
  access: Maybe<IUserAccess>
  permissions: QUserBootstrapPermissions
}

export const UserModel = BaseModel.named('User')
  .props({
    language: types.maybe(types.string),
    soundVolume: types.optional(types.number, 25),
    soundIgnore: false,
  })
  .volatile<IVolatileProps>(() => ({
    isLoaded: false,
    branches: [],
    brands: [],
    soundEnabled: false,
    access: null,
    permissions: {
      canCancelOrders: false,
      canCreateOrder: false,
    },
  }))
  .views(self => ({
    get branchesId() {
      return self.branches.map(R.prop('id'))
    },
    get defaultBranch() {
      if (self.branches.length === 0) {
        throw new Error('No user branches available to get defaultBranchId')
      }
      return self.branches[0]
    },
    filterBranches(predicate: (value: FCompanyBranchFragment) => boolean) {
      return self.branches.filter(predicate)
    },
    canQuery(queryName: string) {
      return (
        self.access !== null && self.access.queries.has(queryName.toLowerCase())
      )
    },
    canMutate(mutationName: string) {
      return (
        self.access !== null &&
        self.access.mutations.has(mutationName.toLowerCase())
      )
    },
    get isReady() {
      return self.isLoaded || !self.root.auth.isAuthenticated
    },
  }))
  .actions(self => ({
    applyUserData(user: QUserBootstrapUser) {
      self.language = user.language.rawCode
      self.branches = user.branches
      self.brands = RM.uniqBy(
        RM.prop('id'),
        user.branches.map(RM.prop('brand')),
      )
    },
    applyPermissions(permissions: QUserBootstrapPermissions) {
      self.permissions = permissions
    },
    applyAccess(access: QUserBootstrapAccess) {
      const makeSet = (fields: ReadonlyArray<QUserBootstrapFields>) =>
        new Set(
          fields.map(
            RM.compose(
              RM.toLower,
              RM.prop('name'),
            ),
          ),
        )

      self.access = {
        queries: makeSet(access.queries.fields ? access.queries.fields : []),
        mutations: makeSet(
          access.mutations && access.mutations.fields
            ? access.mutations.fields
            : [],
        ),
        subscriptions: makeSet(
          access.subscriptions && access.subscriptions.fields
            ? access.subscriptions.fields
            : [],
        ),
      }
    },
    finishBootstrap() {
      self.isLoaded = true
    },
    setLanguage(lang: string) {
      self.language = lang
    },
    controlSound(enabled: boolean = true, ignore: boolean = false) {
      self.soundEnabled = enabled
      self.soundIgnore = ignore
    },
    setSoundVolume(volume: number) {
      self.soundVolume = volume
    },
  }))

interface IUserAccess {
  queries: Set<string>
  mutations: Set<string>
  subscriptions: Set<string>
}

export interface TUserModel extends Instance<typeof UserModel> {}
export interface TUserModelProps extends SnapshotIn<typeof UserModel> {}
