import React from 'react'

import { Spacing } from '../styling'
import { Sizer } from './Sizer'

interface IProps {
  className?: string
}

export const SizerButton: React.FC<IProps> = ({ children, ...props }) => {
  return (
    <Sizer
      width={Spacing.ButtonHeight}
      height={Spacing.ButtonHeight}
      {...props}
    >
      {children}
    </Sizer>
  )
}
