import numeral from 'numeral'

export async function changeNumeralLocale(lang: string) {
  if (lang === 'en') {
    numeral.locale(lang)
    return
  }
  await import(
    /* webpackMode: "lazy", webpackChunkName: "numeral-[index]" */
    `numeral/locales/${lang}`
  )
  numeral.locale(lang)
}

const numberRx = /[\d\.,\s]+/

export function validateNumber(input: string) {
  return numberRx.test(input)
}
