import gql from 'graphql-tag'

import { FGeoPointFragmentDoc } from '../../graph/generated'

export const ZoneLocationQuery = gql`
  query GZoneLocation($zoneId: ID!) {
    zone(id: $zoneId) {
      id
      companyBranch {
        id
        gps {
          ...FGeoPoint
        }
      }
    }
  }
  ${FGeoPointFragmentDoc}
`

export const ZoneBranchLocationQuery = gql`
  query GZoneBranchLocation($branchId: ID!) {
    companyBranch(id: $branchId) {
      id
      gps {
        ...FGeoPoint
      }
    }
  }
  ${FGeoPointFragmentDoc}
`

export const ZoneColorsQuery = gql`
  query GZoneColors {
    zoneColors {
      id
      code
      color
    }
  }
`
