import { LocationDescriptor } from 'history'

import { getQueryErrors } from '../../../graph/errorHandler'
import {
  FMenuCategoryFragment,
  WareCategoryTypeEnum,
} from '../../../graph/generated'
import {
  GMenuCategoryCreate,
  GMenuCategoryDelete,
  GMenuCategoryUpdate,
  GSortMenuCategory,
} from '../../../graph/types'
import { RM } from '../../../helpers/ramda'
import { MenuBaseModel } from './MenuBaseModel'
import {
  MenuCategoryCreateMutation,
  MenuCategoryDeleteMutation,
  MenuCategoryUpdateMutation,
  SortMenuCategoryMutation,
} from './MenuCategoryModel.graphql'

export interface TCategoryFormValues extends FMenuCategoryFragment {}

export const MenuCategoryModel = MenuBaseModel.named('MenuCategory').actions(
  self => ({
    async sortCategory(categoryId: ID, offset: number) {
      const variables: GSortMenuCategory.Variables = {
        categoryId,
        offset,
      }
      const result = await self.client.mutate({
        mutation: SortMenuCategoryMutation,
        variables,
      })
      return Boolean(result.data && result.data.success)
    },
    createCategory(
      formValues: TCategoryFormValues,
      categoryType: WareCategoryTypeEnum,
    ) {
      const variables: GMenuCategoryCreate.Variables = {
        input: {
          companyBranch: self.branchId,
          wareCategoryType: categoryType,
          ...getFormVariables(formValues, categoryType),
        },
      }
      return getQueryErrors(
        self.client.mutate({
          mutation: MenuCategoryCreateMutation,
          variables,
        }),
      )
    },
    async updateCategory(
      formValues: TCategoryFormValues,
      categoryType: WareCategoryTypeEnum,
    ) {
      const variables: GMenuCategoryUpdate.Variables = {
        categoryId: formValues.id,
        input: {
          ...getFormVariables(formValues, categoryType),
        },
      }
      return getQueryErrors(
        self.client.mutate({
          mutation: MenuCategoryUpdateMutation,
          variables,
        }),
      )
    },
    async deleteCategory(categoryId: ID, categoryType: WareCategoryTypeEnum) {
      const variables: GMenuCategoryDelete.Variables = {
        categoryId,
      }
      await self.client.mutate({
        mutation: MenuCategoryDeleteMutation,
        variables,
      })
    },
  }),
)

function getFormVariables(
  formValues: TCategoryFormValues,
  categoryType: WareCategoryTypeEnum,
) {
  return Object.assign({
    nameLabel: formValues.nameLabel,
    internalNoteLabel: formValues.internalNoteLabel,
    enabled: formValues.visible,
    pictogram: formValues.pictogram,
    recipes: formValues.recipes.map(RM.prop('id')),
    minCount: formValues.minCount,
    maxCount: formValues.maxCount,
  })
}

export const getCategoryRoute = (
  recipeId: ID,
  categoryType: WareCategoryTypeEnum,
): LocationDescriptor => ({
  pathname: recipeId,
  state: { categoryType },
})
